<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b>推广员中心</b></el-breadcrumb-item>
        <el-breadcrumb-item>查看推广收益</el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="toolbar" style="margin-top:30px;">
      <el-form :inline="true" :model="filters">
        <el-form-item label="玩家ID">
          <el-input v-model="filters.id" @keyup.enter.native="handleSearch()"></el-input>
        </el-form-item>
        <el-form-item label="推广员ID">
          <el-input v-model="filters.nickname" @keyup.enter.native="handleSearch()"></el-input>
        </el-form-item>
        <el-form-item label="上级推广员ID">
          <el-input v-model="filters.bossId" @keyup.enter.native="handleSearch()"></el-input>
        </el-form-item>
        <el-form-item label="时间" label-width="68px">
          <el-date-picker
            v-model="filters.startTime"
            type="date"
            placeholder="选择日期" style="width:202px;">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="至" label-width="68px" style="margin-left:-44px;">
          <el-date-picker
            v-model="filters.endTime"
            type="date"
            placeholder="选择日期" style="width:202px;">
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="handleSearch()">查询</el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <el-table :data="list" highlight-current-row @selection-change="selsChange"
              style="width: 100%;">
      <el-table-column type="index" width="60"></el-table-column>
      <el-table-column prop="product" label="玩家ID" width="100"></el-table-column>
      <el-table-column prop="number" label="玩家昵称" width="100" sortable></el-table-column>
      <el-table-column prop="productPic" label="头像" width="100">
        <template slot-scope="scope">
          <img :src="scope.row.productPic" alt="" style="width: 50px;height: 50px;">
        </template>
      </el-table-column>
      <el-table-column prop="" label="购买物品" width="100"></el-table-column>
      <el-table-column prop="payType" label="支付金额" width="100"></el-table-column>
      <el-table-column prop="sale" label="推广员ID" width="120"></el-table-column>
      <el-table-column prop="weight" label="推广员收益" width="120"></el-table-column>
      <el-table-column prop="weight" label="上级ID" width="100"></el-table-column>
      <el-table-column prop="weight" label="上级收益" width="100"></el-table-column>
      <el-table-column prop="weight" label="收益时间" width="auto"></el-table-column>
    </el-table>

    <!--工具条-->
    <el-col :span="24" class="toolbar">
      <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="1" :total="total"
                     style="float:right;">
      </el-pagination>
    </el-col>
  </el-row>
</template>

<script>
  import axios from 'axios'
    export default {
        name: "PromotionOfEarnings",
        data(){
          return{
            filters:{},
            list:[],
            total:0
          }
        },
      methods:{
        handleCurrentChange(){},
        selsChange: function (sels) {
          this.sels = sels;
        },
      },
      mounted(){

      }
    }
</script>

<style scoped>

</style>



