<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b>推广员中心</b></el-breadcrumb-item>
        <el-breadcrumb-item>提现申请</el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <div class="stay_out">待处理（0）</div>
      <el-form :inline="true" :model="filters" style="margin-top:10px;">
        <el-form-item label="推广员ID">
          <el-input v-model="filters.nickname" @keyup.enter.native="handleSearch"></el-input>
        </el-form-item>
        <el-form-item label="状态">
          <el-select v-model="filters.upgrade" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="handleSearch">查询</el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!-- 中奖记录列表-->
    <el-table :data="record" highlight-current-row style="width:100%;" id="out-table">
      <el-table-column type="index" width="60"></el-table-column>
      <el-table-column prop="name" label="推广员ID" width="160"></el-table-column>
      <el-table-column prop="desc" label="昵称" width="120"></el-table-column>
      <el-table-column prop="productPic" label="头像" width="100">
        <template slot-scope="scope">
          <img :src="scope.row.productPic" alt="" style="width: 50px;height: 50px;">
        </template>
      </el-table-column>
      <el-table-column prop="type" label="等级" width="120"></el-table-column>
      <el-table-column prop="taskName" label="申请金额" width="120" sortable></el-table-column>
      <el-table-column prop="taskName" label="申请时间" width="120" sortable></el-table-column>
      <el-table-column prop="taskName" label="状态" width="160" sortable></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button size="small" @click="adjustmentDialog(scope.$index,scope.row)">通过</el-button>
          <el-button size="small" type='text'>驳回</el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--点击通过后提示-->
    <el-dialog title="" :visible.sync="passVisible" :close-on-click-modal="false" class="tip">
      <div class="content">
        <div class="title">提示</div>
        <div class="matter">是否通过提现申请？</div>
        <el-button size="small" class="anniu cancel">取消</el-button>
        <el-button size="small" type="primary" class="anniu">通过</el-button>
      </div>
    </el-dialog>

    <!--点击驳回后提示-->
    <el-dialog title="" :visible.sync="rejectVisible" :close-on-click-modal="false" class="tip">
      <div class="content">
        <div class="result">驳回理由</div>
        <el-input type="textarea" class="textarea" style="height:100px;"></el-input>
        <el-button size="small" class="anniu cancel">取消</el-button>
        <el-button size="small" type="primary" class="anniu">通过</el-button>
      </div>
    </el-dialog>

    <!--查看理由-->
    <el-dialog title="" :visible.sync="checkVisible" :close-on-click-modal="false" class="tip">
      <div class="content">
        <div class="result">驳回理由</div>
        <el-input type="textarea" class="textarea" style="height:100px;"></el-input>
        <el-button size="small" type="primary" class="through">通过</el-button>
      </div>
    </el-dialog>
  </el-row>
</template>

<script>
  import axios from 'axios'
  export default {
    name: "DestoonFinanceCard",
    data(){
      return{
        options:[
          {value:'待处理'},
          {value:'已通过'},
          {value:'已驳回'},
        ],
        filters:{},
        record:[],
        passVisible:false,
        rejectVisible:false,
        checkVisible:false
      }
    },
    methods:{
      handleSearch(){

      }
    }
  }
</script>

<style lang="scss">
  .toolbar{
    margin-top:30px;
    float:left;
  }
  .stay_out{
    width:200px;
    height:80px;
    background-color: #ccc;
    text-align: center;
    line-height:80px;
    font-size:30px;
    float:left;
    margin:-10px 30px 0 0;
    cursor: pointer;
  }
  .content{
    width:300px;
    height:200px;
    border:1px solid #000;
    border-radius: 5px;
    .title{
      text-align: center;
      height:50px;
      line-height: 50px;
      border-bottom:1px solid #000;
      font-weight:bold;
      font-size:20px;
    }
    .matter{
      height:100px;
      line-height: 100px;
      border-bottom:1px solid #000;
      text-align: center;
      font-size:18px;
    }
    .anniu{
      margin-top:8px;
    }
    .cancel{
      margin-left:90px;
    }
    .result{
      height:50px;
      line-height:50px;
      font-size:18px;
      margin-left:20px;
    }
    .through{
      margin:10px 0 0 120px;
    }
  }
  .tip{
    .el-dialog{
      width:25%;
    }
  }
  .textarea{
    margin:0 20px;
    width:260px;
    height:100px;
    .el-textarea__inner {
      height: 100px;
    }
  }

</style>



